.viewer-container{background:#FFFFFF;height:100%;width:80%;margin:0 1%;display:inline-block;float:left;padding:120px 2% 320px;z-index:800;position:absolute;right:0}.viewer-container img[src$='#dfg-logo']{max-width:400px}.viewer-container img[src$='#atomic-design']{max-width:600px}.viewer-container a.anchor{padding-top:120px;margin-top:-120px;display:inline-block;outline:none}.viewer-container .md-card{margin:10px 0 28px;-webkit-box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12);box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)}.viewer-container .md-card__title{background-color:#282a36;padding:0.5em;color:#FFF}.viewer-container .md-card__content{padding:0.9em}.viewer-container hr{margin:30px 0;display:inline-block;width:100%}.viewer-container pre{border:1px solid #ccc}.viewer-container blockquote{color:#666;margin:0;padding-left:3em;border-left:0.5em #eee solid}.viewer-container table{border-collapse:collapse;border-spacing:0}.viewer-container table tr{border-top:1px solid #c6cbd1;background:#fff}.viewer-container table th,.viewer-container table td{padding:6px 13px;border:1px solid #dfe2e5}.viewer-container table th{background:#333333;color:#FFFFFF}.viewer-container table tr:nth-child(2n){background:#f6f8fa}



